.App {
  text-align: center;
  height: 100vh;
  overflow: scroll;
}

.light{
  --bg: white;
  --text: black;
}

.dark{
  --bg: black;
  --text: black;
}

.controls{
  position: fixed;
  bottom: 0;
  height: 8vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content:space-around;
  
  overflow: hidden;
  box-shadow: rgba(100, 100, 111, 0.5) 0px 7px 29px 0px;
  background-color: white;
}

.control{
  background: none;
  border: none;
  width: 100%;
  /* border: 1px solid red; */
  height: 300%;
  border-radius: 50%;
  transition: 100ms;
  /* background-color: rgb(224, 214, 182); */
}

.control:active{
  background-color: rgb(224, 214, 182);
}



.s1 p{
  font-size: 15px;
  text-align: left;
  padding: 20px;
  font-family: 'Poppins';
  font-weight: 300;
}

.s3{
  position: relative;
  height: 150vh;
  display: flex;
  justify-content: center;
  margin-top: 30px;
}

.s3_content{
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.card{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  height: 45vh;
  width: 70vw;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 0px 25px;
  padding: 10px;
}

.text{
  display: flex;
  flex-direction: column;
}

#heading{
  font-size: 30px;
}

.text p{
  margin: 0;
  text-align: left;
}

.img{
  height: 15vh;
  width: 15vh;
  border-radius: 10px;
  transform: translate(-50px, -50px);
} 


.loader{
  position: fixed;
  top: 50vh;
  left: 50vw;
  transform: translate(-50%, -50%);
  display: grid;
  box-shadow: black 0px 0px 50px;
  grid-template-columns: 1fr 5fr;
  align-items: center;
  padding: 5px;
  width: 150px;
  height: 70px;
  background-color: white;
}

#loaderIcon{
  transition: 100ms;
  animation: 3s linear 0s infinite running Loader;
}



@keyframes Loader {
  0%{
    transform: rotateZ(0deg);
  }
  25%{
    transform: rotateZ(90deg);
  }
  50%{
    transform: rotateZ(180deg);
  }

  75%{
    transform: rotateZ(270deg);
  }
  100%{
    transform: rotateZ(360deg);
  }
}

.blur{
  filter: blur(3px);
  opacity: 50%;
}